  },
  beforeMount() {
    if (this.$store.state.user.name !== "") {
      document.getElementById("home-link").click();
    }
  },
  methods: {
    async showPopup(message) {
      const confirmation = await this.$refs.popup.show({
        message,
        confirmText: "OK",
      });
      if (confirmation) {
        console.log("ok");
      }
    },
    changeBoard(newBoard, e) {
      this.board = newBoard;
      document.querySelector(".exact-board").classList.remove("exact-board");
      e.currentTarget.classList.add("exact-board");
    },
    async loginUser(username, password) {
      try {
        const loginResult = await axios.get(
          `${this.$store.state.backend_link}users/read/${username}/${password}`
        );
        console.log(loginResult);
        this.$store.commit("setupUser", {
          origin: "login",
          newData: loginResult.data,
        });
        document.getElementById("settings-link").click();
      } catch (error) {
        if (error.response.status === 400) {
          this.showPopup(error.response.data);
        }
      }
    },
    async signUser() {
      // username and password validation :
      let usernamePattern = /^[\u0621-\u064A0a-z]{3,14}$/;
      let passwordPattern = /^[^"]{6,20}$/;

      if (!usernamePattern.test(this.newUser.name)) {
        this.showPopup(this.appVocubulary.validations.username);
        return;
      }

      if (!passwordPattern.test(this.newUser.password)) {
        this.showPopup(this.appVocubulary.validations.password);
        return;
      }

      // age validation :
      if (this.newUser.age > 60 || this.newUser.age < 16) {
        this.showPopup(this.appVocubulary.validations.age);
        return;
      }

      // country validation :
      if (!this.country_list.includes(this.newUser.country)) {
        this.showPopup(this.appVocubulary.validations.country);
        return;
      }

      // gender validation :statuser before submitting please !",
      if (this.newUser.gender !== "male" && this.newUser.gender !== "female") {
        this.showPopup(this.appVocubulary.validations.gender);
        return;
      }

      try {
        await axios.post(
          `${this.$store.state.backend_link}users/add/`,
          this.newUser
        );
        this.loginUser(this.newUser.name, this.newUser.password);
      } catch (error) {
        this.showPopup(error.response.data);
      }
    },
  },
};
